<template>
    <v-page>
    	<div slot="header">
            <v-header>
                <v-btn :icon="'icon-back'" v-link.back></v-btn>
                <h1>实名认证</h1>
            </v-header>
        </div>
    	<div class="bindCard-wrap">
    		<v-schedule :step="1"></v-schedule>
    		<div class="bindCard-content">   			
	    		<img src="../images/realName.png" />
	    		<p class="desc1">拍摄身份证完善身份信息</p>
	    		<p class="desc2">确保图片清晰  四角完整</p>
    		</div>
    		<div class="btn-card">
            	<v-linear-button :btnTxt="btnTxt"  @click="front"></v-linear-button>
       		 </div>    		
    	</div>
        
    </v-page>
</template>

<script>
    import { vLinearButton } from '../../component/linearButton'
    import {vHeader, vBtn} from '@base/header'
    import {vSchedule} from '../../component/schedule'
    export default {
        name: 'startPhoto',
        components: {
            vLinearButton,
            vSchedule
        },
        data () {
            return {
				btnTxt: '立即拍摄',
				frontData: {},
				reverseData: {}
            }
        },
        methods:{
        	front() {
				let vm = this
				T.distOCR("0").then(function(ret) {
					vm.frontData = ret
					vm.reverse()
					console.log(ret)
				}).catch(function(ret) {
					console.log(ret)
				})
			},
			reverse() {
				let vm = this
				T.distOCR("1").then(function(ret) {
					vm.reverseData = ret
					T.go({
						path: "/main/identification/index",
						params: {
							frontData: vm.frontData,
							reverseData: vm.reverseData
						}
					})
				}).catch(function(ret) {
					console.log(ret)
				})
			}
        }

    }
</script>

<style lang="scss" scoped>
	.bindCard-wrap{
		display: flex;
	    flex-direction: column;
	    justify-content: space-between;
	    align-items: center;
	    .btn-card{
	        width: t(592);
	        height: t(90);
	        margin-top: t(84);
	    }
	    .bindCard-progress{
	    	img{
	    		width: 100%;
	    	}
	    }
	    .bindCard-content{
	    	display: flex;
		    flex-direction: column;
		    background: #fff;
		    align-items: center;
	    	width:  t(694);
	    	height: t(400);
	    	img{
	    		width: t(182);
	    		height: t(160);
	    		margin-top: t(60);
	    	}
	    	.desc1{
	    		font-size: t(35);
	    		margin-top: t(60);
	    	}	
	    	.desc2{
	    		color:#aeaeae;
	    		margin-top: t(20);
	    	}
	    	    	
	    }
	} 
</style>



